import React from "react";
import "./styles.css";

export default class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      temperature: 0,
      lat: 0,
      long: 0,
      wind: 0,
      humidity: 0,
      feelslike: 0,
      city: "Bangalore"
      // name: " "
    };
    // this.handleChange = this.handleChange.bind(this);
    // this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.getData("Madurai");
  }
  getData = (value) => {
    fetch(
      "https://api.openweathermap.org/data/2.5/forecast?q=" +
        value +
        "&appid=3f1d127e94cb07c66bf7026f274bb021"
    )
      .then((response) => response.json())
      .then((json) => {
        this.setState({
          temperature: json.list[0].main.temp,
          humidity: json.list[0].main.humidity,
          feelslike: json.list[0].main.feels_like,
          lat: json.city.coord.lat,
          long: json.city.coord.lon,
          wind: json.list[0].wind.speed,
          city: json.city.name,
          weatherType: json.list[0].weather[0].main,
          weatherDescription: json.list[0].weather[0].description,
          icon: json.list[0].weather[0].icon,
          country: json.city.country
        });
      })
      .catch((error) => {
        console.log(error);
      });
  };

  // handleChange(event) {
  //   this.setState({ value: event.target.value });
  // }
  // handleSubmit(event) {
  //   console.log("A name was submitted: " + this.state.value);
  //   event.preventDefault();
  // }

  render() {
    return (
      <div>
        <h1 className="heading">WEATHER APP</h1>
        {/* <form onSubmit={this.handleSubmit}>
          <label>
            Name:
            <input
              type="text"
              value={this.state.value}
              onChange={this.handleChange}
            />{" "}
          </label>
          <input type="submit" value="Submit" />
        </form> */}
        <div
          className="city"
          onClick={() => {
            this.getData("Bangalore");
          }}
        >
          Bangalore
        </div>
        <div
          className="city"
          onClick={() => {
            this.getData("Delhi");
          }}
        >
          Delhi
        </div>
        <div
          className="city"
          onClick={() => {
            this.getData("Mumbai");
          }}
        >
          Mumbai
        </div>

        <div className="container">
          {/* WEATHER COMPONENT */}
          <div className="weather">
            <div className="container-inner">
              {/* LEFT SIDE CONTENT */}
              <div className="content-inner left-side">
                <span className="primary">{this.state.city}</span>
                <br />
                <span className="secondary">As of {new Date().toString()}</span>
                <br />
                <br />

                <span className="temp">{this.state.temperature}</span>
                <br />
                <br />

                <span className="primary">{this.state.weatherType}</span>
                <br />
                <span className="secondary">
                  {this.state.weatherDescription}
                </span>
                <br />
              </div>
              {/* RIGHT SIDE CONTENT */}
              <div className="content-inner right-side">
                <span className="primary">{this.state.humidity}</span>
                <br />
                <span className="secondary">Humidity</span>
                <br />
                <br />

                <span className="primary">{this.state.feelslike}</span>
                <br />
                <span className="secondary">Feels like</span>
                <br />
                <br />

                <span className="primary">{this.state.wind}</span>
                <br />
                <span className="secondary">Wind</span>
                <br />
              </div>
            </div>
          </div>
          {/* DETAILS COMPONENT */}
          <div className="details">
            <div className="container-inner">
              {/* LEFT SIDE CONTENT */}
              <div className="content-inner left-side">
                <br />
                <br />
                <span className="primary">{this.state.city}</span>
                <br />
                <span className="secondary">Details</span>
                <br />
                <br />
                <br />

                <span className="primary">{this.state.country}</span>
                <br />
                <span className="secondary">Country</span>
                <br />
                <br />
              </div>
              {/* RIGHT SIDE CONTENT */}
              <div className="content-inner right-side">
                <br />
                <br />
                <span className="primary">
                  {new Date().getDate()}-{new Date().getMonth() + 1}-
                  {new Date().getFullYear()}
                </span>
                <br />
                <span className="secondary">Local Time</span>
                <br />
                <br />
                <br />

                <span className="primary">
                  {this.state.lat} , {this.state.long}
                </span>
                <br />
                <span className="secondary">Co-ordinates</span>
                <br />
                <br />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
